@import "../general/mixins.scss";
@import "../general/variables.scss";

.page-article-video__title {
  @include heading-font;
  margin: 0;
  padding-top: 17px;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    padding-top: 57px;
  }

  @media (min-width: $desktop-width) {
    font-size: 30px;
    padding-top: 62px;
  }
}

.page-article-video__text {
  @include basic-font;
  margin: 0;
  padding-top: 26px;
  padding-bottom: 40px;

  @media (min-width: $tablet-width) {
    width: 445px;
    margin: 0 auto;
    padding-bottom: 95px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 90px;
  }
}
