}

.button {
  color: #ffffff;
  background-color: #81b3d2;
  text-decoration: none;
  border: none;
  transition: 100ms all ease;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}

