.main-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.main-header__logo {
  position: absolute;
}
@media (min-width: 768px) {
  .main-header__logo {
    z-index: 10;
  }
}

.main-header__nav-toggle {
  position: relative;
  padding: 0;
  top: 14px;
  left: 129px;
  width: 22px;
  height: 24px;
  border: none;
  background-image: url("../../img/menu-icon.svg");
  background-color: #ffffff;
  background-repeat: no-repeat;
  background-position: center;
  transition: 100ms all ease;
  cursor: pointer;
}
.main-header__nav-toggle:hover {
  opacity: 0.6;
}
.main-header__nav-toggle:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .main-header__nav-toggle {
    visibility: hidden;
  }
}
.nav-menu {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 5;
}
@media (min-width: 320px) and (max-width: 768px) {
  .nav-menu--closed {
    display: none;
  }
}

.nav-menu__close-toggle {
  position: absolute;
  padding: 0;
  top: 17px;
  left: 278px;
  width: 23px;
  height: 23px;
  border: none;
  background-image: url("../../img/icon-cross.svg");
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  transition: 100ms all ease;
  cursor: pointer;
}
.nav-menu__close-toggle:hover {
  opacity: 0.6;
}
.nav-menu__close-toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .nav-menu__close-toggle {
    display: none;
  }
}

.nav-menu__list {
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #5b90ab;
}
@media (min-width: 768px) {
  .nav-menu__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 768px;
    background-color: #ffffff;
  }
}
@media (min-width: 1200px) {
  .nav-menu__list {
    width: 1200px;
  }
}

.nav-menu__item {
  display: flex;
  flex-wrap: wrap;
  min-width: 320px;
  min-height: 55px;
  justify-content: center;
  flex-direction: column;
  background-color: #81b3d2;
  transition: 100ms all ease;
}
.nav-menu__item:hover {
  background-color: #669ec0;
}
.nav-menu__item:active {
  background-color: #5496bd;
}
@media (min-width: 320px) and (max-width: 768px) {
  .nav-menu__item--empty {
    display: none;
  }
  .nav-menu__item:last-child {
    position: relative;
  }
  .nav-menu__item:last-child::after {
    content: "";
    position: absolute;
    bottom: 0;
    height: 7px;
    width: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (min-width: 768px) {
  .nav-menu__item {
    min-width: 153px;
    background-color: #ffffff;
  }
  .nav-menu__item:hover, .nav-menu__item:active {
    background-color: #ffffff;
  }
}
@media (min-width: 1200px) {
  .nav-menu__item {
    min-width: 240px;
  }
}
.nav-menu__item--catalog {
  margin-top: 1px;
  border-top: solid 1px #8fc2de;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .nav-menu__item--catalog {
    margin-right: 153px;
  }
}
@media (min-width: 1200px) {
