.radio-button__mark {
  position: absolute;
  margin-top: -11px;
  width: 46px;
  height: 46px;
  border: none;
  background-color: #f2f2f2;
  border-radius: 50%;
  transition: 100ms all ease;
  cursor: pointer;
}
.radio-button__mark:hover {
  background-color: #e5e5e5;
}
.radio-button__mark:active {
  background-color: #d5d5d5;
}

.radio-button__input:disabled + .radio-button__mark {
  opacity: 0.2;
}
.radio-button__input:disabled + .radio-button__mark:hover {
  background-color: #f2f2f2;
}

.radio-button__input:checked + .radio-button__mark::after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #333333;
  content: "";
}

.radio-button__text {
  padding-left: 62px;
  display: block;
}

.checkbox__mark {
  position: absolute;
  margin-top: -11px;
  width: 46px;
  height: 46px;
  border: none;
  border-radius: 4px;
  background-color: #f2f2f2;
  transition: 100ms all ease;
}
.checkbox__mark:hover {
  background-color: #e5e5e5;
}
.checkbox__mark:active {
  background-color: #d5d5d5;
}

.checkbox__input:disabled + .checkbox__mark {
  opacity: 0.2;
}
.checkbox__input:disabled + .checkbox__mark:hover {
  background-color: #f2f2f2;
}

.checkbox__input:checked + .checkbox__mark::before {
  position: absolute;
  width: 22px;
  height: 16px;
  top: 14px;
  left: 12px;
  background-image: url("../../img/tick-icon.svg");
  background-repeat: no-repeat;
  content: "";
}

.checkbox__text {
  padding-left: 62px;
  display: block;
}